@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200');

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden; /* Prevent horizontal scroll */
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Ensure the body takes up full height */
}

/* Base styles (desktop and larger screens) */
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  width: 100%;
}

.hero {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2vw;
  height: 90vh;
  background: url('../pics/index_bg.png');
  background-size: cover;
  background-position: center;
  overflow: hidden;
  max-width: 100vw;
  margin: 0 auto;
}

.hero-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 8vw;
  box-sizing: border-box;
  position: relative;
}

.hero-title {
  position: relative;
  text-shadow: 4px 4px 4px #000;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #fff;
  font-size: 6.5vw;
  font-family: Merriweather, sans-serif;
  margin: 0;
  z-index: 100;
}

.hero-subtitle {
  position: relative;
  font-weight: 400;
}

button {
  border: 0;
  background: transparent;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

button:hover {
  transform: scale(1.1);
}

.action {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  width: 120px;
  font-size: 16px;
  color: #21005d;
  font-weight: 800;
  white-space: nowrap;
  letter-spacing: 0.1px;
  line-height: 143%;
  margin: 1rem 1rem;
  background: #FF4742;
  border: 1px solid #FF4742;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  font-family: nunito, roboto, proxima-nova, "proxima nova", sans-serif;
  outline: 0;
  padding: 12px 14px;
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
}

.action:hover,
.action:active {
  background-color: white;
  color: #FF4742;
  transform: scale(1.2);
  transition: 0.4s;
}

.separ {
  margin: 0;
  padding: 1rem 1rem;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  background-color: #fff;
  color: #000;
  text-align: center;
  font: 400 48px Merriweather, sans-serif;
}

.wht {
  background-color: black;
  color: white;
}

.ylw {
  background-color: #fcd535;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 90%;
  align-items: center;
  justify-content: center;
  margin-top: 3vh;
  gap: 3vw;
  padding-bottom: 1rem;
}

.why-flip-box {
  background-color: transparent;
  perspective: 1000px;
  border-radius: 20px;
  aspect-ratio: 0.94;
  text-align: center;
  width: 30%;
}

.why-flip-box-inner {
  position: relative;
  width: 100%;
  height: 100%;
  aspect-ratio: 0.94;
  border-radius: 15%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.why-flip-box:hover .why-flip-box-inner {
  transform: rotateY(180deg);
}

.why-flip-box-front, .why-flip-box-back {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.why-flip-box-front {
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  text-shadow: 1px 1px 2px white, 0 0 1em white, 2px 2px 0.1em white;
  font-size: 4vw;
  font-weight: 600;
  font-family: Merriweather, sans-serif;
}

.why-flip-box-back {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: dodgerblue;
  color: white;
  transform: rotateY(180deg);
  padding: 0;
  line-height: 1.5;
}

p {
  background-color: rgba(299, 299, 299, 0.5);
  width: 100%;
}

#why-bg1 {
  background: url('../pics/1why.png');
  background-size: cover;
