.section-who-item-txt {
  display: flex;
  flex-direction: column;
  line-height: normal;
  padding: 0 2rem 0 0;
}

h3 {
  text-align: center;
  color: white;
}

.footer {
  justify-content: center;
  align-items: center;
  background-color: #000;
  color: #fff;
  text-align: center;
  letter-spacing: 0.1px;
  padding: 0 60px;
  font-size: 18px;
  font-weight: 400;
  margin: 0;
  height: 100px;
}

/* Media Queries for responsiveness */

/* Tablets (768px and up) */
@media (max-width: 1024px) {
  .hero-grid{
    justify-content: space-between;
  }
  .hero {
    grid-template-columns: 1fr;
    height: 80vh;
  }

  .hero-title {
    font-size: 8vw;
    text-align: center;
    font-weight: 900
  }

  .hero-subtitle {
    width: 35%;
    text-wrap: wrap;
    font-size: 3vw;
    text-align: left;
    transform: translateX(-150px);
    font-family: Merriweater, serif;
    font-weight: 700;
    font-style: italic;
    color: black;

  font-size: clamp(3rem, 10vw, 6rem);
  line-height: 0.9em;
  text-shadow: 3px 1px 1px #fcfcfc, 2px 2px 1px #165bfb, 4px 2px 1px #4af7ff,
    3px 3px 1px #165bfb, 5px 3px 1px #4af7ff, 4px 4px 1px #165bfb,
    6px 4px 1px #4af7ff, 5px 5px 1px #165bfb, 7px 5px 1px #4af7ff,
    6px 6px 1px #165bfb, 8px 6px 1px #4af7ff, 7px 7px 1px #165bfb,
    9px 7px 1px #4af7ff;
  }

  .section {
    flex-direction: column;
    align-items: center;
  }

  .why-flip-box {
    width: 80%;
  }
  .section{
    width: 100vw;
    flex-direction: row
  }
}
