/* Global Styles */
:root {
  --primary-bg: #000;
  --primary-text: #fff;
  --secondary-bg: #fcd535;
  --secondary-text: #181a20;
  --hover-bg: rgba(299, 299, 299, 0.15);
  --active-bg: red;
  --border-color: rgba(44, 44, 44, 1);
}

/* Navigation Bar */
.navbar-container {
  top: 0;
  position:fixed;
  z-index: 1000;
  background-color: var(--primary-bg);
  width: 100%;
}

.navbar {
  position: sticky;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1.5rem;
  background-color: var(--primary-bg);
  width: 100%
}

.nav-left {
  display: flex;
  align-items: center;
  gap: 2vw;
  font-size: 1.5rem;
  color: var(--secondary-bg);
  font-weight: 600;
  white-space: nowrap;
}

.logo {
  width: 35px;
  aspect-ratio: 1;
}

.nav-title {
  font-family: 'Merriweather', serif;
}

.nav-panel {
  display: flex;
  gap: 1vw;
  flex: 1;
}

.nav-panel-element {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.3rem;
  color: var(--primary-text);
  font-size: 1.2rem;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}

.nav-panel-element:hover {
  background-color: var(--hover-bg);
  transform: scale(1.1);
  border-radius: 8px;
}

.nav-panel-element:active {
  background-color: var(--active-bg);
}

.nav-auth {
  display: flex;
  gap: 1vw;
  font-size: 1.2rem;
  font-weight: 500;
  justify-content: end;
}

.nav-auth-sign, .nav-auth-reg {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  white-space: nowrap;
}

.nav-auth-sign {
  border: 1.9px solid var(--border-color);
  background-color: var(--primary-bg);
  color: var(--primary-text);
}

.nav-auth-reg {
  border: 1px solid var(--border-color);
  background-color: var(--secondary-bg);
  color: var(--secondary-text);
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.material-symbols-outlined {
  color: var(--primary-text);
}

.hidden {
  display: none;
}

/* Overlay */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
}

.register-form, .signin-form {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  width: 90%;
  max-width: 400px;
}

/* Responsive Styles */
@media (max-width: 800px) {
  .navbar {
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 0.5rem 1rem;
    background-color: var(--primary-bg);
  }

  .nav-left, .nav-title {
    display: none; /* Hide logo and title */
  }

  .nav-panel {
    justify-content: space-around;
    padding: 0;
  }

  .nav-panel-element {
    display: flex;
    flex-direction: column;
    font-size: 10px
  }

  .nav-auth {
    display: none;
    flex-direction: row;
    justify-content: space-around;
  }

  .nav-auth-sign, .nav-auth-reg {
    font-size: 0.9rem;
    padding: 0.5rem;
  }
}
.reg{
  max-width: 500px;
  height: fit-content;
}