@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200');

/* Global Styles */
:root {
  --primary-bg: #000;
  --primary-text: #fff;
  --secondary-bg: #fcd535;
  --secondary-text: #181a20;
  --hover-bg: rgba(299, 299, 299, 0.15);
  --active-bg: red;
  --border-color: rgba(44, 44, 44, 1);
}

body{
  font-family: Merriweather;
  padding:0;
  margin: 0;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  box-shadow: 5px 20px 50px #000;
}

.overlay-content {
  display: flex;
  flex-direction: column;
  align-content: center;
  background: white;
  padding: 20px;
  border-radius: 5px;
  position: relative;
}


.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: red;
  color: white;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  cursor: pointer;
  font-size: 20px;
}
form, .form-step {
  display: flex;
  flex-direction: column;
  gap: 10px;
  font-family: Merriweather, serif;
}

input {
  padding: 10px;
  margin: 5px 0;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  
  border-radius: 8px;
 
}
.btn{
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 40px;
    background: linear-gradient(to right, #1E2022, #52616B);
    box-shadow: 0 2px 10px rgba(0, 0, 0, .4);
    font-size: 16px;
    text-align: center;
    color: #F0F5F9;
    font-weight: 500;
    cursor: pointer;
    border-radius: 5px;
    border: none;
    outline: none;
}
.btn-gg {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 2vw;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 40px;
  text-align: center;
  background: linear-gradient(to right, #FBF6E2, #E68369);
  box-shadow: 0 2px 10px rgba(0, 0, 0, .4);
  font-size: 16px;
  color: black;
  font-weight: 500;
  cursor: pointer;
  border-radius: 5px;
  border: none;
  outline: none;
  margin-bottom: 1.2rem;
}
svg{
  height: 60%
}

.iconic {
height: 30px;
}
.regInput{
  width: 100%;
  padding: 0.2rem;
}
.navbar-container{
  position: sticky
}

.loader {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  width: fit-content;
  align-self: center;
  font-weight: bold;
  font-family: monospace;
  font-size: 30px;
  background:linear-gradient(90deg,#000 50%,#0000 0) right/200% 100%;
  animation: l21 2s infinite linear;
}
.loader::before {
  content :"Loading...";
  color: #0000;
  padding: 0 5px;
  background: inherit;
  background-image: linear-gradient(90deg,#fff 50%,#000 0);
  -webkit-background-clip:text;
          background-clip:text;
}

@keyframes l21{
  100%{background-position: left}
}
