}

.lesson-page h1{
  padding:0;
  margin: 0;
}

.current-lesson {
  padding: 20px;
}

.next-lesson {
  cursor: pointer;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 10px 0;
}

.lesson-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.lesson-content {
  background: #fff;
  color: #000;
  padding: 20px;
  border-radius: 8px;
  max-width: 800px;
  width: 100%;
  max-height: 90%;
  overflow-y: auto;
}

.close-button {
  align-self: flex-end;
  top: 10px;
  right: 10px;
  background: #f00;
  color: #fff;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

.part {
  margin-bottom: 20px;
}

.part h1 {
  font-size: 24px;
}

.part h2 {
  font-size: 20px;
}

div{
  width: 100%;
  box-sizing: border-box;
}
.lesson-page .container {
  max-width: 100%;
  text-align:justify;
  margin: 0 auto;
  padding:15px;
  background:#fff;
  margin: 18px auto;
