  position:relative;
  -moz-box-shadow: 0px 0px 28px rgba(0,0,0,.3);
  -webkit-box-shadow: 0px 0px 28px rgba(0,0,0,.3);
   box-shadow: 0px 0px 28px rgba(0,0,0,.3);
   overflow:hidden;
}
.lesson-page mark{
    background: linear-gradient(
    -100deg,
    hsla(56, 100%, 50%, 1),
    hsla(48,92%,75%,.7) 95%,
    hsla(48,92%,75%,.1)
    );
    border-radius: 1em 0;
    padding: .15em;
