    padding:0;
}
.var{
  padding: 0.2rem;
  margin: 0 0.3rem;
  border: 1px solid black;
  border-radius: 8px;
}
.mod-det {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7); /* Dark semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  overflow: auto;
}

.mod-det-cont {
  width: 60vw !important;
  height: auto; /* Adjust height to fit content */
  max-height: 90%; /* Prevent from overflowing screen */
  padding: 1.5rem;
  background-color: #A3D8FF; /* Light blue background */
  color: #333;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px; /* Increase gap for more spacing */
  border-radius: 12px;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 4px 8px, rgba(0, 0, 0, 0.1) 0px 2px 4px;
}

.black {
  color: #333; /* Softer black color */
}

.mod-det-head {
  position: relative;
  background-color: #94FFD8; /* Light mint green background */
  color: #2c3e50; /* Darker text color */
  grid-column: 1 / 3;
  padding: 0.4rem;
  border-radius: 8px;
  text-align: center;
  font-family: 'Merriweather', serif;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 4px;
}

.mod-det-head-item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.mod-det-item {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 0;
  gap: 4px; /* Adjust the gap between divs */
}

.mod-det-item > div {
  background-color: #FDFFC2; /* Soft yellow background */
  color: #2c3e50; /* Darker text color */
  font-size: 10px; /* Further reduced font size */
  padding: 0.1rem 0.3rem; /* Minimal padding */
  margin: 0.05rem; /* Minimal margin */
  width: fit-content;
  flex: auto;
  border: 1px solid #FF76CE; /* Pink border */
  border-radius: 4px; /* Further reduced border-radius */
  box-shadow: rgba(60, 64, 67, 0.15) 0px 1px 2px, rgba(60, 64, 67, 0.1) 0px 1px 2px;
  max-width: calc(50% - 0.5rem); /* Maximum width adjusted for even smaller fit */
}

.mod-det-item > div > h2 {
  text-align: center;
  color: #FF76CE; /* Pink color for headings */
  font-family: 'Merriweather', serif;
  font-size: 14px; /* Reduced font size for headings */
  margin: 0.3rem 0; /* Smaller margin for headings */
}

.mod-det-close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  background: #FF76CE; /* Pink background */
  color: #fff;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 4px;
  transition: background 0.3s ease;
}

.mod-det-close-btn:hover {
  background: #FF579A; /* Darker pink on hover */
}


.short{
  width: fit-content !important;
  height:fit-content !important;
  grid-column: 1/3;
  justify-content: center;
}
.long{
  justify-content: space-evenly;
}
.long h2 {
  background-color: #FF76CE; /* Pink background for the heading */
  color: #fff; /* White text for contrast */
  padding: 0.5rem 1rem; /* Padding around the heading */
  margin: -0.3rem -0.3rem 1rem -0.3rem;
  border-radius: 10px 10px 0 0; /* Rounded top corners only */
  font-family: 'Merriweather', serif; /* Fancy serif font */
  font-size: 1.2rem; /* Slightly larger font size */
  text-align: center; /* Center-align the heading */
  box-shadow: inset 0 -2px 5px rgba(0, 0, 0, 0.1); /* Subtle inner shadow for depth */
}

.long  > div{
  font-size: 14px;
  margin: 0.2rem 0; /* Space between paragraphs */
  line-height: 1.2; /* Improved readability */
  background-color: white;
  border-radius: 10px 10px 0 0;
  padding: 0.3rem;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
}

